body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  background-color: #101215;
  color: white;
}

.container {
  display: flex;
  height: 100vh;
  overflow: hidden;
}

.sidebar {
  width: 120px;
  background-color: #101215;
  height: 100vh;
  padding-top: 40px;
  border-right: 1px solid #2a2a2a;
  flex-shrink: 0;
}

.sidebar-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 40px;
  cursor: pointer;
  opacity: 0.7;
  transition: opacity 0.3s;
}

.sidebar-item:hover, .sidebar-item.active {
  opacity: 1;
}

.sidebar-item:hover .icon {
  background-color: #3a3a3a;
  transform: scale(1.05);
  transition: background-color 0.2s, transform 0.2s;
}

.icon {
  width: 60px;
  height: 60px;
  background-color: #2a2a2a;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 24px;
  margin-bottom: 10px;
  transition: background-color 0.2s, transform 0.2s;
}

.label {
  font-size: 18px;
}

.main-content {
  flex: 1;
  padding: 20px;
  overflow-y: auto;
}

.flow-diagram {
  width: 100%;
  height: 350px;
  margin-bottom: 20px;
}

.content-text {
  max-width: 900px;
  padding: 20px;
}

h1 {
  font-size: 28px;
  margin-bottom: 20px;
}

h2 {
  font-size: 24px;
  margin-top: 30px;
  margin-bottom: 15px;
}

p {
  font-size: 16px;
  line-height: 1.6;
  margin-bottom: 15px;
}

ul {
  padding-left: 20px;
  margin-bottom: 20px;
}

li {
  margin-bottom: 8px;
  line-height: 1.6;
}

/* SVG diagram styles */
svg {
  margin: 0 auto;
  display: block;
  max-width: 100%;
  height: auto;
}