/* Demo page specific styles */
.demo-container {
  padding: 20px;
  max-width: 1200px;
  margin: 0 auto;
}

.loading-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 300px;
}

.loading-spinner {
  width: 50px;
  height: 50px;
  border: 5px solid rgba(255, 255, 255, 0.3);
  border-radius: 50%;
  border-top-color: #4382c9;
  animation: spin 1s ease-in-out infinite;
  margin: 20px 0;
}

.loading-spinner.small {
  width: 20px;
  height: 20px;
  border-width: 3px;
  margin-right: 10px;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

.mode-switcher {
  display: flex;
  margin-bottom: 20px;
}

.mode-button {
  background-color: #2a2a2a;
  color: white;
  border: none;
  padding: 10px 20px;
  margin-right: 10px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.2s;
}

.mode-button:hover {
  background-color: #3a3a3a;
}

.mode-button.active {
  background-color: #4382c9;
}

.detection-container {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  margin-bottom: 30px;
}

.video-container {
  position: relative;
  width: 640px;
  height: 480px;
  background-color: #000;
  border-radius: 8px;
  overflow: hidden;
}

video, .uploaded-image {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.detection-canvas {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.control-overlay {
  position: absolute;
  bottom: 10px;
  left: 10px;
  z-index: 10;
}

.control-button, .cloud-process-button {
  background-color: #4382c9;
  color: white;
  border: none;
  padding: 10px 15px;
  border-radius: 5px;
  cursor: pointer;
  font-weight: bold;
  transition: background-color 0.2s, transform 0.1s;
}

.control-button:hover, .cloud-process-button:hover {
  background-color: #5392d9;
  transform: translateY(-2px);
}

.control-button:active, .cloud-process-button:active {
  transform: translateY(1px);
}

.control-button.active {
  background-color: #e74c3c;
}

.cloud-process-button {
  margin-bottom: 15px;
  font-size: 16px;
  padding: 12px 20px;
  background-color: #7b68b8;
}

.cloud-process-button:hover {
  background-color: #8b78c8;
}

.cloud-process-button:disabled {
  background-color: #555;
  cursor: not-allowed;
  opacity: 0.7;
  transform: none;
}

.server-processing-controls {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 15px;
  padding: 10px;
  background-color: rgba(0, 0, 0, 0.2);
  border-radius: 8px;
}

.detection-count {
  margin: 5px 0 0 0;
  font-size: 14px;
  color: #aaa;
}

.results-container {
  flex: 1;
  min-width: 300px;
  max-height: 480px;
  overflow-y: auto;
  background-color: #1a1a1a;
  border-radius: 8px;
  padding: 15px;
}

.processing-indicator {
  display: flex;
  align-items: center;
  margin: 10px 0;
  padding: 10px;
  background-color: rgba(67, 130, 201, 0.2);
  border-radius: 5px;
}

.detection-cards {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.detection-card {
  background-color: #2a2a2a;
  border-radius: 8px;
  padding: 15px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
}

.detection-card h3 {
  margin-top: 0;
  margin-bottom: 10px;
  color: #4382c9;
  text-transform: capitalize;
}

.card-content {
  display: flex;
  justify-content: space-between;
}

.detection-info {
  flex: 1;
}

.attributes h4, .object-info h4 {
  margin-bottom: 5px;
  color: #b8d6f2;
}

.attributes ul, .object-info ul {
  margin-top: 5px;
  padding-left: 20px;
}

.no-detections {
  padding: 20px;
  text-align: center;
  color: #999;
}

.tip {
  margin-top: 15px;
  font-style: italic;
  font-size: 14px;
  color: #7b68b8;
  background-color: rgba(123, 104, 184, 0.1);
  padding: 10px;
  border-radius: 5px;
}

.image-upload-input {
  margin: 20px;
  color: white;
}

.demo-info {
  background-color: #1a1a1a;
  border-radius: 8px;
  padding: 20px;
  margin-top: 20px;
}

.demo-info h2 {
  color: #b8d6f2;
  margin-top: 0;
}

.demo-info ol {
  padding-left: 20px;
}

.demo-info li {
  margin-bottom: 10px;
}

/* Responsive adjustments */
@media (max-width: 1200px) {
  .detection-container {
    flex-direction: column;
  }
  
  .video-container {
    width: 100%;
    max-width: 640px;
  }
  
  .results-container {
    width: 100%;
    max-width: 640px;
  }
}