/* Tech Stack page specific styles */
.tech-stack-container {
  padding: 20px;
  max-width: 1000px;
  margin: 0 auto;
}

.tech-section {
  margin-bottom: 40px;
}

.tech-section h2 {
  color: #4382c9;
  margin-bottom: 20px;
  padding-bottom: 10px;
  border-bottom: 1px solid #333;
}

.tech-cards {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
}

.tech-card {
  background-color: #1a1a1a;
  border-radius: 8px;
  padding: 20px;
  width: calc(33.333% - 14px);
  min-width: 250px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
  transition: transform 0.2s, box-shadow 0.2s;
}

.tech-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
}

.tech-card h3 {
  color: #b8d6f2;
  margin-top: 0;
  margin-bottom: 10px;
}

.tech-card p {
  margin: 0;
  line-height: 1.5;
}

.flow-diagram-ts {
  display: flex;
  flex-direction: column;
  gap: 30px;
  padding: 20px;
  background-color: #1a1a1a;
  border-radius: 8px;
}

.flow-step {
  display: flex;
  align-items: flex-start;
  gap: 20px;
}

.flow-icon {
  min-width: 40px;
  height: 40px;
  background-color: #4382c9;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  font-weight: bold;
}

.flow-description h3 {
  margin-top: 0;
  margin-bottom: 10px;
  color: #b8d6f2;
}

.flow-description p {
  margin: 0;
  line-height: 1.5;
}

.security-list {
  background-color: #1a1a1a;
  border-radius: 8px;
  padding: 20px 20px 20px 40px;
}

.security-list li {
  margin-bottom: 15px;
  line-height: 1.5;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .tech-card {
    width: 100%;
  }
}